<template>
  <div class="hello">
  <div class="background">
      <img :src="imgSrc" width="100%" height="100%" alt="" />
  </div>

  <van-row>
      <van-col span="8"></van-col>
  </van-row>
    <h1>{{ msg }}</h1>

      
    <div style="margin-top:270px">
    <van-form @submit="onSubmit">
      <van-cell-group>
        <van-field
        v-model="useremail"
        label-width=60
        type="email"
        left-icon="contact"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"/>
        <van-field
        v-model="password1"
        label-width=60
        type="password"
        left-icon="closed-eye"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"/>
        <van-field
        v-model="password2"
        label-width=60
        type="password"
        left-icon="closed-eye"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"/>
        <van-field
        v-model="userphone"
        label-width=60
        type="digit"
        left-icon="phone-o"
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"/>
        <van-field
        v-model="username"
        type="text"
        left-icon="contact"
        label-width=60
        name=""
        label=""
        placeholder=""
        :rules="[{ required: true, message: '' }]"/>
        <van-field
        :rules="[{ required: true, message: '' }]"
        v-model="yzm"
        center
        clearable
        label=""
        placeholder="">
        <template #button>
          <van-button size="small" type="primary"></van-button>
        </template>
        </van-field>
      </van-cell-group>
    
    <div style="margin-top: 16px;">
    <van-button round block type="warning" native-type="submit" ></van-button>
    </div>
    </van-form>
  </div>


    <div>
        
        <p><van-button round plain block type="warning" to="/MyInfo"></van-button></p>
        <p></p>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Register',
  data () {
    return {
      msg: '',
      useremail: '123@qq',
      password1: '123',
      password2: '123',
      userphone: '178',
      username: 'czt',
      yzm: '',
      imgSrc:require('../assets/citynight1.jpg'),
    };
  },
  methods: {
    onSubmit(values) {
      console.log('submit', values);
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
  color:white
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.background{
    width:100%;  
    height:50%;  /**100% */
    z-index:-1;
    position: absolute;
}

</style>



